import React, { Component, Fragment } from "react";
import CSSTransitionGroup from "react-transition-group/CSSTransitionGroup";
import classnames from "classnames";
import Tabs from "react-responsive-tabs";
import {
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink,
  Row,
  Col,
  Card,
  CardBody,
  CardHeader,
  CardFooter,
  Button,
  ButtonGroup,
  UncontrolledButtonDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Container,
  ListGroup,
  ListGroupItem,
  Form, FormGroup, Label, Input
} from "reactstrap";
import CountUp from "react-countup";
import PerfectScrollbar from "react-perfect-scrollbar";
import {
  faAngleUp,
  faAngleDown,
  faDotCircle,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Link } from 'react-router-dom';
import ApiCall from "../../../Services/Function/ApiCall";
import Loading from '../../../Loading';
import Alert from '../../../Alert';
import bg3 from "../../../../assets/utils/images/dropdown-header/city2.jpg";
import {
  setLoaderFlag,
  setNotificationFlag,
  setUserData
} from '../../../../reducers/ThemeOptions';
import {connect} from 'react-redux';
import avatar1 from "../../../../assets/utils/images/avatars/2.jpg";
import './profile.css';
class index extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user:{
        profile:null,
        fname:null,
        lname:null,
        username:null,
        phone:null,
        password:null,
      },
      error_image:null,
      error:'Only Image Allow..!',
      message:'Data Succeffully Update',
      type:'success',
    };
  }
  HandleSetState=(key,value)=>
  {
    let user=this.state.user;
    user[key]=value;
    this.setState({
      'user':user
    });
  }
  HandleImageError=(message)=>
  {
    this.setState({
      'error_image':message
    });
  }
  HandleProfile=async(e)=>
  {
    let flag=(e.target.files[0]['type']).includes("image");
    if(flag)
    {
      let base64 = await this.convertBase64(e.target.files[0]);
      this.HandleSetState('profile',JSON.stringify(base64));
      this.HandleImageError('');
    }
    else
    {
      this.HandleImageError(this.state.error);
      e.target.value = null;
      return e;
    }
  }
  convertBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader();
      fileReader.readAsDataURL(file)
      fileReader.onload = () => {
        resolve(fileReader.result);
      }
      fileReader.onerror = (error) => {
        reject(error);
      }
    })
  }
  removeNull=()=>{
    let data={};
    for (const [key, value] of Object.entries(this.state.user)) {
      if(value)
      {
        data[key]=value;
      }
    }
    return data;
  }
  HandleUpdate=async()=>
  {
    let user_data=this.removeNull();
    let user=JSON.parse(localStorage.getItem('user'));
    if(Object.keys(user_data).length > 0)
    {
      this.toggleEnableSubmit();
      let data={
        'data':user_data,
        'model':'User',
        'id':user['id'],
        'is_hash':1,
      }
      let response=await ApiCall.postAPICall(2,'update',data);
      await localStorage.setItem('user', JSON.stringify(response.data));
      let {setUserData } = this.props;
      await setUserData(response.data);
      let { setNotificationFlag } = this.props;
      setNotificationFlag(true);
      this.toggleEnableSubmit();
    }
  }
  toggleEnableSubmit = () => {
    let { loaderFlag, setLoaderFlag } = this.props;
    setLoaderFlag(!loaderFlag);
  };
  
  HandleDisplay=()=>
  {
    let userdata=JSON.parse(localStorage.getItem('user'));
    let image=userdata['profile']?JSON.parse(userdata['profile']):avatar1;
    let data =
      <div className="container">
        <div className="row gutters">
          <div className="col-xl-3 col-lg-3 col-md-12 col-sm-12 col-12">
            <div className="card">
              <div className="card-body">
                <div className="account-settings">
                  <div className="user-profile">
                    <div className="user-avatar">
                      <img src={image} alt="Pacra Admin"/>
                    </div>
                    <h5 className="user-name">{userdata.username}</h5>
                    <h6 className="user-email">{userdata.email}</h6>
                  </div>
                  <div className="about">
                    {<span className="text-danger">{this.state.error_image}</span>}
                    <Input type="file" onChange={this.HandleProfile} name="profile" id="profile" />
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="col-xl-9 col-lg-9 col-md-12 col-sm-12 col-12">
            <div className="card">
              <div className="card-body">
                <div className="row gutters">
                  <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                    <h6 className="mb-2 text-primary">Personal Details</h6>
                  </div>
                  <Col xl="6" lg="6" md="6" sm="6" >
                    <FormGroup>
                      <Label for="fname">First Name</Label>
                      <Input type="text" defaultValue={userdata.fname} onChange={(e)=>{this.HandleSetState('fname',e.target.value)}} name="fname" id="fname" placeholder="First Name..."/>
                    </FormGroup>
                  </Col>
                  <Col xl="6" lg="6" md="6" sm="6" >
                    <FormGroup>
                      <Label for="lname">Last Name</Label>
                      <Input type="text" defaultValue={userdata.lname} onChange={(e)=>{this.HandleSetState('lname',e.target.value)}} name="lname" id="lname" placeholder="Last Name..."/>
                    </FormGroup>
                  </Col>
                  <Col xl="6" lg="6" md="6" sm="6" >
                    <FormGroup>
                      <Label for="eMail"><span className="text-danger">*</span> Email</Label>
                      <Input type="email" readOnly value={userdata.email} id="eMail" placeholder="Enter email "/>
                    </FormGroup>
                  </Col>
                  <Col xl="6" lg="6" md="6" sm="6" >
                    <FormGroup>
                      <Label for="password">Password</Label>
                      <Input type="password" id="password" onChange={(e)=>{this.HandleSetState('password',e.target.value)}} placeholder="Enter Password "/>
                    </FormGroup>
                  </Col>
                  <Col xl="6" lg="6" md="6" sm="6" >
                    <FormGroup>
                      <Label for="username">Username</Label>
                      <Input type="text" id="username" defaultValue={userdata.username} onChange={(e)=>{this.HandleSetState('username',e.target.value)}}  placeholder="Username"/>
                    </FormGroup>
                  </Col>
                  <Col xl="6" lg="6" md="6" sm="6" >
                    <FormGroup>
                      <Label for="phone">Phone</Label>
                      <Input type="number" id="phone" defaultValue={userdata.phone} onChange={(e)=>{this.HandleSetState('phone',e.target.value)}} placeholder="Enter phone number"/>
                    </FormGroup>
                  </Col>
                </div>
                <div className="row gutters">
                  <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                    <div className="text-right">
                      {!this.props.loaderFlag ?
                        <Button className="mb-2 mr-2 btn-gradient-info" onClick={()=>{this.HandleUpdate()}}> Update</Button>
                        :
                        <Loading/>
                      }
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    return data;
  }
  render() {
    return (
      <Fragment>
        <CSSTransitionGroup component="div" transitionName="TabsAnimation" transitionAppear={true}
          transitionAppearTimeout={0} transitionEnter={false} transitionLeave={false}>
          <Alert type={this.state.type} message={this.state.message}/>
          <Container fluid>
            {this.HandleDisplay()}
          </Container>
        </CSSTransitionGroup>
      </Fragment>
    );
  }
}

const mapStateToProps = state => ({
  loaderFlag: state.ThemeOptions.loaderFlag,
  notificationFlag:state.ThemeOptions.notificationFlag,
  userData:state.ThemeOptions.userData,
});

const mapDispatchToProps = dispatch => ({
  setLoaderFlag: enable => dispatch(setLoaderFlag(enable)),
  setNotificationFlag:enable=>dispatch(setNotificationFlag(enable)),
  setUserData:enable=>dispatch(setUserData(enable)),
});

export default connect(mapStateToProps, mapDispatchToProps)(index);
